h4 {
  color: #ffffff;
  color: var(--color3);
  font-size: 0.8rem;
  font-weight: 200;
  /* font-style: italic; */
}

h2 {
  color: #ffffff;
  color: var(--color3);
  font-size: 1rem;
  font-weight: 300;
}

::-webkit-calendar-picker-indicator {
  background-image: url(/static/media/calendar-outline.2461ad16.svg);
  -webkit-transition: all 150ms ease;
  transition: all 150ms ease;
}

::-webkit-calendar-picker-indicator:hover {
  -webkit-filter: brightness(1.2);
          filter: brightness(1.2);
}


.text-gradient {
  background: -webkit-linear-gradient(45deg, var(--main-orange-color), var(--ion-tab-bar-color-selected));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-animation: fadeInUp 250ms ease forwards;
          animation: fadeInUp 250ms ease forwards;
}

.link-gradient {
  background: -webkit-linear-gradient(45deg, var(--main-orange-color), var(--ion-tab-bar-color-selected));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-animation: fadeInUp 250ms ease forwards;
          animation: fadeInUp 250ms ease forwards;
  transition: 150ms ease;
}

.link-gradient:hover {
  -webkit-filter: brightness(1.2);
          filter: brightness(1.2);
}

.background-gradient {
  background: #10b1bd;
  background: var(--main-orange-color);
  background: linear-gradient(69deg, #10b1bd, #6215dd 120%);
  background: linear-gradient(69deg, #10b1bd, var(--ion-tab-bar-color-selected) 120%);
}



@-webkit-keyframes fadeInUp {
  0% {
    opacity: 0;
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}



@keyframes fadeInUp {
  0% {
    opacity: 0;
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@-webkit-keyframes fadeInLeft {
  0% {
    opacity: 0;
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}

@keyframes fadeInLeft {
  0% {
    opacity: 0;
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}


@-webkit-keyframes fadeInRight {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}


@keyframes fadeInRight {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}



@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
    -webkit-transform: translate(0px, 0px) rotate(0deg);
            transform: translate(0px, 0px) rotate(0deg);
  }

  100% {
    opacity: 0;
    -webkit-transform: translate(0px, 200px) rotate(-6deg);
            transform: translate(0px, 200px) rotate(-6deg);
  }
}



@keyframes fadeOut {
  0% {
    opacity: 1;
    -webkit-transform: translate(0px, 0px) rotate(0deg);
            transform: translate(0px, 0px) rotate(0deg);
  }

  100% {
    opacity: 0;
    -webkit-transform: translate(0px, 200px) rotate(-6deg);
            transform: translate(0px, 200px) rotate(-6deg);
  }
}

@media (max-width: 780px) {
  body {
    padding: 2rem;

  }
