.expense-item {
    position: relative;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 1.2rem;
    margin: 1rem 0;
    border-radius: var(--borderRadius1);
    background-color: var(--ion-card-background);
    background: var(--main-orange-color);
    background: linear-gradient(30deg, #10b1bd 1%, var(--ion-tab-bar-color-selected) 120%);
    color: var(--color5);
    -webkit-animation: fadeInUp 250ms ease forwards;
            animation: fadeInUp 250ms ease forwards;
}

.expense-item button {
    border: none;
    position: absolute;
    top: -0.5rem;
    right: -0.5rem;
    background: rgb(68, 68, 68);
    box-shadow: 1px 1px 3px 1px rgba(78, 78, 78, 0.226);
    color: white;
    font-weight: bold;
    padding: 0.5rem 0.7rem;
    border-radius: 50px;
    font-size: 0.7rem;
    transition: all 150ms ease;
    opacity: 0;
    -webkit-animation: fadeInLeft 250ms ease 250ms forwards;
            animation: fadeInLeft 250ms ease 250ms forwards;
}

.expense-item button:hover {
    -webkit-filter: brightness(1.1);
            filter: brightness(1.1);
    box-shadow: 1px 1px 8px 10px rgba(90, 90, 90, 0.206);
}

.hide {
    -webkit-animation: fadeOut 350ms ease-in-out forwards;
            animation: fadeOut 350ms ease-in-out forwards;
}

.expense-item__description {
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-end;
    flex: 1 1;
}

.expense-item h2 {
    flex: 1 1;
    margin: 0 0rem;
    font-size: 1rem;

}

.expense-item__price {
    font-size: 1rem;
    font-weight: 600;
    color: var(--color3);
    padding: 0rem;
}

/* ------- Expense date styles --------- */

.expense-item__date {
    padding: 0.8rem 1.2rem;
    border: dashed 1px var(--color3);
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    border-radius: 30px 0px 0px 0px;
}

.expense-item__date h2 {
    margin-bottom: 0.2rem;
}

.expense-item__date .number {
    font-weight: 600;
    font-size: 1.2rem;
}

.expense-item__date .month {
    font-size: 0.7rem;
}

/* ------ Styles for non-mobile -------- */

@media (min-width: 580px) {
    .expense-item__description {
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        flex: 1 1;
    }

    .expense-item__description h2 {
        font-size: 1.2rem;
        font-weight: 600;
        margin: 0 1rem;
    }

    .expense-item__price {
        font-size: 1.25rem;
        padding: 0.5rem 1.5rem;
    }
}
.expenses__msg {
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: top;
    -webkit-animation: fadeInUp 250ms ease forwards;
            animation: fadeInUp 250ms ease forwards;
}

.expenses__msg p {
    font-size: 0.8rem;
    font-weight: 100;
    color: var(--ion-text-color);
    opacity: 0.8;
}
