    margin-bottom: 2rem;
}

.expenses-filter__control {
    display: flex;
    width: 100%;
    align-items: baseline;
    justify-content: space-between;
    grid-gap: 1rem;
    gap: 1rem;
}

.expenses-filter label {
    color: var(--bgColor2);
}

.expenses-filter select {
    font: inherit;
    font-weight: 800;
    background-color: transparent;
    padding: 0.75rem 0.75rem;
    border: none;
    border-bottom: 1px solid;
    border-image-slice: 1;
    border-image-source: linear-gradient(90deg, var(--main-orange-color) 20%, var(--ion-tab-bar-color-selected) 95%);
    -webkit-appearance: none;
            appearance: none;
    border-radius: 0px;
