    transition: all 150ms ease;
    background: -webkit-linear-gradient(320deg, var(--main-orange-color), var(--ion-tab-bar-color-selected));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.expenses-filter select:hover, .csv-div:hover {
