.icon:nth-of-type(1) {
    -webkit-animation: colorShine1 10s ease infinite;
            animation: colorShine1 10s ease infinite;
}

.icon:nth-of-type(2) {
    -webkit-animation: colorShine2 10s ease 50ms infinite;
            animation: colorShine2 10s ease 50ms infinite;
}

.icon:nth-of-type(3) {
