@import url('https://fonts.googleapis.com/css2?family=Questrial&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;1,100;1,200;1,300&display=swap');

/*
  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/
*, *::before, *::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  scroll-behavior: smooth;
}


/* html, body {
  height: 100%;
} */

body {
  /* line-height: 1.5; */
  -webkit-font-smoothing: antialiased;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

#root, #__next {
  isolation: isolate;
}

button {
  cursor: pointer;
}

html {
  font-family: 'Questrial', sans-serif;
  /* letter-spacing: -1px; */
  transition: all 150ms ease-in-out;
}

:root {
  /* colors */
  --bgColor: rgb(244, 255, 248);
  --bgColor2: #09927b;
  --color1: rgb(146, 78, 78);
  --color2: rgb(82, 206, 236);
  --color3: #ffffff;
  --color4: rgb(17, 116, 62);
  --color5: rgb(63, 63, 63);
  /* box shadows */
  --boxShadow1: 0 0.075em 0.6em 0.1em rgba(58, 58, 58, 0.322);
  /* border radius */
  --borderRadius1: 2.5em 0px 0em 0px;

  --main-orange-color: #10b1bd;
  --ion-background-color: #ffffff;
  --ion-background-color-rgb: 0, 0, 0;
  --ion-text-color: #a2a2a2;
  --ion-text-color-rgb: 255, 255, 255;
  --ion-item-background: #141a22;
  --ion-card-background: #141a22;
  --ion-tab-bar-color: #141d86;
  --ion-tab-bar-background: #0e1016;
  --ion-tab-bar-color-selected: #6215dd75;
  --ion-tab-bar-color-selected: #6215dd;
  --ion-toolbar-background: #0e1016;
  --ion-toolbar-border-color: #0e1016;
  --ion-tab-bar-border-color: #0e1016;
  --ion-grid-column-padding: 0;
}

