  background-attachment: fixed;
  background-size: cover;
  padding: 3rem;
  min-height: 200vh;
}

h4 {
  color: var(--color3);
  font-size: 0.8rem;
  font-weight: 200;
  /* font-style: italic; */
}

h2 {
  color: var(--color3);
  font-size: 1rem;
  font-weight: 300;
}

::-webkit-calendar-picker-indicator {
  background-image: url(/src/images/calendar-outline.svg);
  transition: all 150ms ease;
}

::-webkit-calendar-picker-indicator:hover {
  filter: brightness(1.2);
}


.text-gradient {
  background: -webkit-linear-gradient(45deg, var(--main-orange-color), var(--ion-tab-bar-color-selected));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: fadeInUp 250ms ease forwards;
}

.link-gradient {
  background: -webkit-linear-gradient(45deg, var(--main-orange-color), var(--ion-tab-bar-color-selected));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: fadeInUp 250ms ease forwards;
  transition: 150ms ease;
}

.link-gradient:hover {
  filter: brightness(1.2);
}

.background-gradient {
  background: var(--main-orange-color);
  background: linear-gradient(69deg, #10b1bd, var(--ion-tab-bar-color-selected) 120%);
}



@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(10px);
  }

  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}

@keyframes fadeInLeft {
  0% {
    opacity: 0;
    transform: translateX(10px);
  }

  100% {
    opacity: 1;
    transform: translateX(0px);
  }
}


@keyframes fadeInRight {
  0% {
    opacity: 0;
    transform: translateX(-10px);
  }

  100% {
    opacity: 1;
    transform: translateX(0px);
  }
}



@keyframes fadeOut {
  0% {
    opacity: 1;
    transform: translate(0px, 0px) rotate(0deg);
  }

  100% {
    opacity: 0;
    transform: translate(0px, 200px) rotate(-6deg);
  }
}

@media (max-width: 780px) {
  body {
    padding: 2rem;

  }
}